import React from "react";
import { SearchOutlined } from "@ant-design/icons";
import { Select, DatePicker, Button, Space, Breadcrumb, PageHeader, Input, InputNumber, Form } from 'antd';
import { BrowserRouter as Router, Route, Switch, Redirect, useHistory } from 'react-router-dom'
const { Option } = Select;
class EmployeeSearch extends React.Component {
    constructor() {
        super();

        this.state = {

            SearchBy: "SearchByName"
        };
        this.handleChange = this.handleChange.bind(this);

    }
    handleChange(value) {
        this.setState({
            SearchBy: value
        })
    }
    onFinish = (values) => {
        console.log(values)


    };
    onChange(value, dateString) {
        console.log('Selected Time: ', value);
        console.log('Formatted Selected Time: ', dateString);
    }

    onOk(value) {
        console.log('onOk: ', value);
    }
    onFinishFailed = (errorInfo) => {

    };


    render() {
        return (
            <div className="container">


                <PageHeader
                    className="site-page-header"

                    title={[<Space size="large">

                        {this.state.SearchBy === "SearchByStatus" ?
                            <Breadcrumb>
                                <Breadcrumb.Item>Tt c</Breadcrumb.Item>

                                <Breadcrumb.Item>ang c hiu lc</Breadcrumb.Item>
                                <Breadcrumb.Item>ht hiu lc</Breadcrumb.Item>
                            </Breadcrumb> : null}

                    </Space>]}
                    extra={[

                        <Form
                            name="basic"
                            className="search-form"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={this.onFinish}
                            onFinishFailed={this.onFinishFailed}>
                            <Space size="large">
                                <Select defaultValue="SearchByName" onChange={this.handleChange}>
                                    <Option value="SearchByPhone">tm kim theo s in thoi</Option>
                                    <Option value="SearchByName">tm kim theo tn </Option>

                                    <Option value="SearchByStatus">tm kim theo trng thi</Option>


                                </Select>
                                {this.state.SearchBy === "SearchByName" ?
                                    <> <Input name="searchValue" />
                                        <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}
                                {this.state.SearchBy === "SearchByPhone" ? <> <Input name="searchValue" />
                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></> : null}



                            </Space>
                        </Form>
                    ]}
                >



                </PageHeader>

            </div>);
    }
}
export default EmployeeSearch;