import React from "react";
import { SearchOutlined } from "@ant-design/icons";
import { Select, DatePicker, Button, Space, Breadcrumb, PageHeader, Input, InputNumber } from 'antd';
import { BrowserRouter as Router, Route, Switch, Redirect, useHistory } from 'react-router-dom'
import Form from "antd/lib/form/Form";
const { Option } = Select;
const { RangePicker } = DatePicker;
class ContractSearch extends React.Component {
    constructor() {
        super();

        this.state = {

            SearchBy: "SearchByContractId"
        };
        this.handleChange = this.handleChange.bind(this);

    }
    handleChange(value) {
        this.setState({
            SearchBy: value
        })
    }
    onFinish = (values) => {
        console.log(values)


    };
    onChange(value, dateString) {
        console.log('Selected Time: ', value);
        console.log('Formatted Selected Time: ', dateString);
    }

    onOk(value) {
        console.log('onOk: ', value);
    }
    onFinishFailed = (errorInfo) => {

    };

    render() {
        return (

            <div className="container">


                <PageHeader
                    className="site-page-header"

                    title={[<Space size="large">
                        {this.state.SearchBy === "SearchByStatus" ?
                            <Breadcrumb>
                                <Breadcrumb.Item>Tt c</Breadcrumb.Item>
                                <Breadcrumb.Item>
                                    ang duyt
                            </Breadcrumb.Item>
                                <Breadcrumb.Item>
                                    ang ch k
                            </Breadcrumb.Item>
                                <Breadcrumb.Item>ang c hiu lc</Breadcrumb.Item>
                                <Breadcrumb.Item>ht hiu lc</Breadcrumb.Item>
                            </Breadcrumb> : null}


                    </Space>]}
                    extra={[
                        <Form
                            name="basic"
                            className="search-form"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={this.onFinish}
                            onFinishFailed={this.onFinishFailed}>
                            <Space size="large">
                                <Select defaultValue="SearchByContractId" onChange={this.handleChange}>
                                    <Option value="SearchByContractId">tm kim theo s hp ng</Option>
                                    <Option value="SearchByContractDuration">tm kim theo thi hn</Option>
                                    <Option value="SearchByCustomerTaxCode">tm kim theo m s thu</Option>
                                    <Option value="SearchByStatus">tm kim theo trng thi</Option>
                                    <Option value="SearchByContractValue">tm kim theo gi tr hp ng</Option>
                                    <Option value="SearchByContractDeadline">tm kim theo ngy ht hn</Option>
                                    <Option value="SearchByContractCreatedDate">tm kim theo ngy to</Option>
                                </Select>
                                {this.state.SearchBy === "SearchByContractId" ?
                                    <> <Input name="searchValue" />
                                        <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}
                                {this.state.SearchBy === "SearchByCustomerTaxCode" ? <> <Input name="searchValue" />
                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></> : null}
                                {this.state.SearchBy === "SearchByContractValue" ? <> <InputNumber

                                    formatter={value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                    parser={value => value.replace(/\$\s?|(,*)/g, '')}

                                />
                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}
                                {this.state.SearchBy === "SearchByContractDuration" ? <><RangePicker
                                    showTime={{ format: 'HH:mm' }}
                                    format="YYYY-MM-DD HH:mm"
                                    onChange={this.onChange}
                                    onOk={this.onOk}
                                />
                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}
                                {this.state.SearchBy === "SearchByContractDeadline" ? <>
                                    <DatePicker showTime onChange={this.onChange} onOk={this.onOk} />
                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}
                                {this.state.SearchBy === "SearchByContractCreatedDate" ? <>
                                    <DatePicker showTime onChange={this.onChange} onOk={this.onOk} />

                                    <Button type="primary" htmlType="submit" shape="circle" icon={<SearchOutlined />} /></>
                                    : null}

                            </Space>
                        </Form>
                    ]}
                >



                </PageHeader>

            </div>

        );
    }
}
export default ContractSearch;